.overview {
    .column {
        &.contestant {
            margin: 4em 2em;
        }

        .name {
            white-space: pre-wrap;

            margin-top: auto;
            margin-bottom: 1em;

            font-size: 1.2em;
        }
    }

    .questions {
        display: flex;
        flex-direction: column;

        align-items: center;

        .question {
            background: linear-gradient(180deg, rgba(15, 100, 102, 1) 20%, rgba(210, 232, 227, 1) 50%, rgba(15, 100, 102, 1) 80%);
            padding: 1.5em 3em;

            &.highlighted {
                border-color: white;
            }

            &.disabled {
                background: linear-gradient(180deg, rgba(196, 196, 196, 1) 0%, rgba(210, 232, 227, 1) 50%, rgba(196, 196, 196, 1) 100%);
            }
        }
    }
}