.join {
    & input {
        box-shadow: 0 0 20px 5px #D8B08C;
        text-align: center;
        padding: 1em;
        margin-block: 1em;
        border-color: transparent;

        &.invalid {
            border-color: orangered;
        }

        &:focus {
            outline: none;
        }
    }
}
