html,
.expanded {
    height: 100%;
    width: 100%;
}

html {
    background: linear-gradient(315deg, rgba(210, 232, 227, 1) 25%, rgba(255, 203, 154, 1) 50%, rgba(216, 176, 140, 1) 70%, rgba(15, 100, 102, 1) 90%);
}

html,
button,
input {
    color: #2C3532;
    font-size: 1em;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    margin: 0;
}

.centered {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

button,
input {
    border-width: 2px;
    border-style: solid;
    border-radius: .5em;
}

button {
    border-color: #2C3532;
    font-weight: 900;
}

.columns {
    display: flex;
    flex-direction: row;
    margin-inline: auto;

    .column {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: min-content;

        padding: 1em;

        font-weight: bold;
        text-align: center;

        &.contestant {
            border: 2px dashed transparent;
            flex-grow: 1;
            flex-basis: 0;

            width: revert;
            min-width: 0;

            &.mark {
                border-color: #2C3532;
                border-radius: 1em;
            }
        }
    }
}

.home,
.join,
.lobby {
    & button {
        background: linear-gradient(180deg, rgba(210, 232, 227, 1) 0%, rgba(176, 198, 193, 1) 50%, rgba(210, 232, 227, 1) 100%);
        padding: 2em 4em;
        margin-block: 2em;
    }
}